.form-container {
    width: 300px;
    height: 500px;
    background-color: white;
    display: flex;
    justify-content: center;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

input,
textarea {
    margin: 10px 0 10px 0;
}